class OrderButton extends Component {
    constructor(props) {
        super(props);
        this.order = false
        this.playBack = false
        this.error = {
            text: 'sure'
        }
    }

    backVideo(text) {
        if (text == 'order') {
            if (this.props.order == true) {
                return
            } else {
                $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
                    if (data.error_no !== '0') {
                        if (IsPcOrNot.get()) {
                            window.instance.show();
                        }
                        else {
                            $('#login-wrap').css("display", "block");
                            $('#login').css("display", "block");
                        }
                    }
                    else {
                        $.get(Constant.url + 'orderVideo.do', (data) => {
                            // console.log(data.data)
                            if (data.error_no == '0') {
                                this.props.changeState("order")
                            }
                            else {
                                this.error.text = "not_bound_phone";
                                this.setState(this.error);
                                this.refs.Menu.open();
                            }
                        });
                    }
                });

            }
        }

        if (text == 'backtip') {
            if (this.props.playBack == true) {
                return
            } else {
                $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
                    if (data.error_no !== '0') {
                        if (IsPcOrNot.get()) {
                            window.instance.show();
                        } else {
                            $('#login-wrap').css("display", "block");
                            $('#login').css("display", "block");
                        }
                    }
                    else {
                        $.get(Constant.url + 'playBackReminding.do.do', (data) => {
                            // console.log(data.data)
                            if (data.error_no == '0') {
                                this.props.changeState("playback")
                            }
                            else {
                                this.error.text = "not_bound_phone";
                                this.setState(this.error);
                                this.refs.Menu.open();
                            }
                        });
                    }
                });
            }
        }

    }

    render() {
        return <div>
            <Menu ref="Menu" text={this.error.text}/>
            <div onClick={this.backVideo.bind(this, this.props.text)} className='base_btn_c' style={{
                position: "relative",
                width: IsPcOrNot.get() ? "200px" : '1.63rem',
                height: IsPcOrNot.get() ? "60px" : '.47rem',
                color: "#fff",
                textAlign: "center",
                backgroundColor: this.props.bgColor,
                cursor: "pointer",
                display: "flex",
                zIndex: "10",
                fontSize: IsPcOrNot.get() ? '20px' : '.2rem',
                alignItems: "center",
                justifyContent: "center"
            }}>
                <FormattedMessage id={this.props.text}/>
            </div>
        </div>
    }
}

class OrderArea extends Component {
    constructor(props) {
        super(props);
        this.order = false
        this.playBack = false
    }

    componentDidMount() {
        $.get(Constant.url + 'ifOrderVideo.do', (data) => {
            if (data.data.isOrderVideo == 1) {
                this.order = true
                this.setState({
                    order: this.order
                })
            }
            if (data.data.isPlayback == 1) {
                this.playBack = true
                this.setState({
                    playBack: this.playBack
                })
            }
        });
    }

    changeState = (flag) => {
        // console.log(flag)
        if (flag == 'playback') {
            this.playBack = true
            this.setState({
                playBack: this.playBack
            })
        }
        if (flag == 'order') {
            this.order = true
            this.setState({
                order: this.order
            })
        }
    }

    render() {
        return IsPcOrNot.get() ?
            <div style={{
                position: 'relative',
                width: '1200px',
                margin: '0 auto',
                padding: '50px 0'
            }}>
                <div style={{
                    width: "670px",
                    margin: '0 auto',
                    height: "80px",
                    display: 'flex',
                    alignItems: "center",
                    justifyContent: 'space-between',
                    position: 'relative'
                }}>
                    <div style={{
                        width: '50px',
                        height: '50px',
                        background: 'url(' + pc_order + ') no-repeat center center',
                        backgroundSize: 'cover',
                        position: 'absolute',
                        left: '0'
                    }}></div>
                    <div style={{marginLeft: IsPcOrNot.get() ? '80px' : '1.4rem'}}>
                        <OrderButton bgColor='#49c1c7' text={this.playBack == true ? 'already_backtip' : 'backtip'}
                                     order={this.order}
                                     playBack={this.playBack} changeState={this.changeState}/>
                    </div>
                    <div style={{marginRight: IsPcOrNot.get() ? '80px' : '1.4rem'}}>
                        <OrderButton bgColor='#0080ff' text={this.order == true ? 'already_order' : 'order'}
                                     order={this.order}
                                     playBack={this.playBack} changeState={this.changeState}/>
                    </div>
                    <div style={{
                        width: '50px',
                        height: '50px',
                        background: 'url(' + pc_play_tip + ') no-repeat center center',
                        backgroundSize: 'cover',
                        position: 'absolute',
                        right: '0'
                    }}></div>
                </div>
            </div>
            :
            <div style={{
                position: 'relative'
            }}>
                <div style={{
                    width: "100%",
                    height: "1.45rem",
                    display: 'flex',
                    alignItems: "center",
                    justifyContent: 'space-between',
                    padding: '0 0.17rem'
                }}>
                    <div style={{
                        width: '.45rem',
                        height: '.45rem',
                        background: 'url(' + m_play_tip + ') no-repeat center center',
                        backgroundSize: 'cover',
                        position: 'absolute',
                        right: '.9rem'
                    }}></div>
                    <div style={{marginLeft: '1.4rem'}}>
                        <OrderButton bgColor='#49c1c7' text={this.playBack == true ? 'already_backtip' : 'backtip'}
                                     order={this.order}
                                     playBack={this.playBack} changeState={this.changeState}/>
                    </div>
                    <div style={{marginRight: '1.4rem'}}>
                        <OrderButton bgColor='#0080ff' text={this.order == true ? 'already_order' : 'order'}
                                     order={this.order}
                                     playBack={this.playBack} changeState={this.changeState}/>
                    </div>
                    <div style={{
                        width: '.45rem',
                        height: '.45rem',
                        background: 'url(' + m_order + ') no-repeat center center',
                        backgroundSize: 'cover',
                        position: 'absolute',
                        left: '.9rem'
                    }}></div>
                </div>
            </div>
    }
}

