class TerminalC extends Component {
    constructor(props) {
        super(props)
        this.showTerminal = false
    }

    componentDidMount() {
        let element = this.refs.terminalBox;
        var create = lottie.loadAnimation({
            container: element, // the dom element that will contain the animation
            renderer: 'svg',
            loop: true,
            autoplay: true,
            path: '/json/c.json',// the path to the animation json
        });
    }

    showTerminalBox = (event) => {
        this.showTerminal = !this.showTerminal
        this.setState({
            showTerminal: this.showTerminal
        })
    }
    goTerminal = () => {
        browserHistory.push(Constant.router_preffix + '/terminal');
    };

    render() {
        return (
            IsPcOrNot.get() ? <div ref='terminalBox' className='terminal-box' onClick={this.showTerminalBox} style={{
                width: '130px',
                height: '130px',
                backgroundSize: 'cover',
                position: 'absolute',
                bottom: '0px',
                right: '0',
                zIndex: '999',
                cursor: 'pointer'
            }}>
            </div> : <div ref='terminalBox' onClick={this.goTerminal} style={{
                width: '1.4rem',
                height: '1.4rem',
                backgroundSize: 'cover',
                borderRadius: '50%',
                position: 'fixed',
                right: '.3rem',
                bottom: '8%',
                zIndex: '99'
            }}>
            </div>
        )
    }
}

