export class IndexPage extends Component {
    constructor(props) {
        super(props);
        this.agendaPlan = localStorage['language'] == 'en' ? en.agendaPlanData : zh.agendaPlanData
        this.subForums = [];
    }

    componentDidMount() {
        $.get('/json/agenda.json'+"?timestamp=" + new Date().getTime(), (data) => {
            this.subForums = data.slice(1);
            this.setState({
                subForums: this.subForums
            })
        })
    }

    login = () => {
        IsPcOrNot.get() ? window.instance.show() : window.instance.render($("#login")[0]);
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC agendaPlan={this.agendaPlan} subForums={this.subForums}/> :
                    <Phone agendaPlan={this.agendaPlan} subForums={this.subForums}/>
            }
        </div>
    }
}

export default IndexPage;





