"use strict";
import React, {Component} from 'react'
import './terminal.scss'
import lottie from 'lottie-web'
import IsPcOrNot from "../../../components/lib/IsPcOrNot";
import Header from '../../../components/Header/Header'
import {browserHistory} from 'react-router'

import Footer from '../../../components/Footer/Footer'
import Constant from "../../../components/lib/Constant";

class TerminalInput extends Component {
    constructor(props) {
        super(props);
        this.inputValue = '';
        this.isOnFocus = false;
        this.now = 0;
        this.initData = [
            {name: 'login:', value: 'root'},
            {name: 'password:', value: '******'},
            {name: 'AI-DEV', ip: 'BaiDu:', symbol: '~', value: '#'}
        ]
        this.descList = [
            'Welcome to Create 2019 LTS (GNU/Linux 3.11.0-26-generic x86_64)',
            '',
            '170+ AI ability can be updated',
            '',
            'New release \'2019.07.03 LTS\' available. Run \'create\' to upgrade to it',
            ''
        ]
    }

    componentDidMount() {
        this.inputValue = '';
        let str = this.initData[this.now].value;
        if (str) {
            let length = str.length;
            let now = 0;
            var initTimeLine = setInterval(() => {
                if (now <= length - 1) {
                    this.inputValue += str[now];
                    this.setState({
                        inputValue: this.inputValue
                    })
                    now++;
                } else {
                    now = 0;
                    this.props.outPutList.push({result: this.initData[this.now].name + this.initData[this.now].value})
                    this.now++
                    this.props.changeOutPut();
                    this.setState({
                        now: this.now,
                    })
                    if (this.now == this.initData.length - 1) {
                        this.inputValue = ''
                        this.setState({
                            inputValue: this.inputValue
                        })
                        this.props.renderSlowLine(this.descList);
                        clearTimeout(initTimeLine);
                    }
                }
            }, 100)
        }
    }

    inputOnChange(event) {
        this.inputValue = event.target.value;
        this.setState({
            inputValue: this.inputValue
        });
    }

    InputOnFocus = () => {
        this.refs.clipboard.focus();
        this.isOnFocus = true;
        this.setState({
            isOnFocus: this.isOnFocus
        })
    }
    InputOnBlur = () => {
        this.refs.clipboard.blur();
        this.isOnFocus = false;
        this.setState({
            isOnFocus: this.isOnFocus
        })
        IsPcOrNot.get()?null:window.scroll(0,0);
    }
    KeyUpHandle = (e) => {
        //
        if (e.keyCode === 13) {
            this.props.enterHandle(this.inputValue);
            this.inputValue = '';
            this.setState({
                inputValue: this.inputValue
            })
        }
    }

    render() {
        return (
            <div className="cmd" onClick={this.InputOnFocus} ref='cmdView'
                 style={{width: '100%', overflow: 'hidden'}}>
                {
                    this.props.nameFlag ?
                        <span className="prompt">
                        <span style={{
                            fontWeight: 'blod',
                            color: this.now == this.initData.length - 1 ? '#73c9e3' : '#fff'
                        }}>
                            {this.initData[this.now].name}
                        </span>
                            {
                                (this.now == this.initData.length - 1) ?
                                    <span>
                                    <span>@</span>
                                    <span style={{color: '#78d065'}}>{this.initData[this.now].ip}</span>
                                    <span style={{color: '#fced78'}}>{this.initData[this.now].symbol}</span>
                                    <span>#</span>
                                </span> : null
                            }
                    </span> : null
                }
                <span>{this.inputValue}</span>
                <span className={this.isOnFocus ? 'cursor blink' : 'cursor'}>&nbsp;&nbsp;</span>
                <span></span>
                <textarea
                    ref='clipboard'
                    onChange={this.inputOnChange.bind(this)}
                    onFocus={this.InputOnFocus}
                    onBlur={this.InputOnBlur}
                    value={this.inputValue}
                    onKeyUp={this.KeyUpHandle.bind(this)}
                    style={{
                        backgroundColor: "#000"
                    }}
                    className="clipboard">
                </textarea>
            </div>
        )
    }
}

class TerminalOutput extends Component {
    constructor(props) {
        super(props)
        this.outPutList = []
        this.cmdList = ['create', 'info', 'agenda', 'courses', 'speakers', 'where', 'register', 'AI', 'help', 'whoami', 'exit',];
        this.showMoreFlag = false;
        this.nameFlag = false;
        this.progressSymbol = ''
        this.progressNumber = 0;

    }

    componentDidMount() {
    }

    changeOutPut = () => {
        this.setState({
            outPutList: this.outPutList
        })
    }
    intoView = () => {
        let container;
        setTimeout(() => {
            container = this.refs.terminal;
            container.scrollTop = container.scrollHeight;
        })
    }
    enterHandle = (cmd) => {
        cmd = cmd.trim();
        let hasCmd = false;
        for (var i = 0; i < this.cmdList.length; i++) {
            if (this.cmdList[i] == cmd) {
                hasCmd = true;
            }
        }
        if (cmd != 'createStart') {
            let cmdLog = {name: 'AI-DEV', ip: 'BaiDu:', symbol: '~', value: cmd}
            this.outPutList.push({cmdLog: cmdLog});
        }
        if (!hasCmd && cmd != 'createStart' && cmd !== '' && cmd !== 'ls' && cmd !=='ll' && cmd !=='pwd') {
            this.outPutList.push({result: cmd + ' : command not found, type \'help\' for more info'});
        }
        if (cmd == 'createStart') {
            this.nameFlag = false;
            this.setState({
                nameFlag: this.nameFlag
            }, () => {
                this.lottieRender('createStartBox', '/json/createJson/CREATE.json');
            })
        }
        if (cmd == 'create') {
            this.nameFlag = false;
            this.setState({
                nameFlag: this.nameFlag
            }, () => {
                this.lottieRender('create-box', IsPcOrNot.get() ? '/json/hengban/hengban.json' : '/json/shuban/shuban.json');
            })
        }
        if (cmd == 'info') {
            this.nameFlag = false;
            this.setState({
                nameFlag: this.nameFlag
            }, () => {
                this.lottieRender('infoBox', '/json/info.json');
            })
        }
        if (cmd == 'register' || cmd == 'exit' || cmd == 'AI') {
            this.outPutList.push({progress: 'register'})
            this.nameFlag = false;
            this.setState({
                nameFlag: this.nameFlag
            })
            if (this.props.flag) {
                this.props.flag.value = false;
                this.setState(this.props.flag);
            }
            let n = 0;
            let progressTimeLine = setInterval(() => {
                n = n + 4;
                this.progressSymbol += '#'
                this.progressNumber += 4;
                this.setState({
                    progressSymbol: this.progressSymbol,
                    progressNumber: this.progressNumber
                })
                if (this.progressSymbol.length == 26) {
                    clearTimeout(progressTimeLine)
                    this.progressSymbol = ''
                    this.progressNumber = 0;
                    this.outPutList.pop();
                    this.nameFlag = true;
                    this.setState({
                        progressSymbol: this.progressSymbol,
                        progressNumber: this.progressNumber,
                        outPutList: this.outPutList,
                        nameFlag:this.nameFlag
                    })
                    if (cmd == 'register') {
                        browserHistory.push(Constant.router_preffix + '/ticket_type');
                    }
                    if (cmd == 'exit') {
                        browserHistory.push(Constant.router_preffix + '/');
                    }
                    if (cmd == 'AI') {
                       location.href = 'https://ai.baidu.com';
                    }
                }
            }, 20)
        }
        if (cmd == 'speakers') {
            $.get('json/speakers.json'+"?timestamp=" + new Date().getTime(), (data) => {
                this.showInfo(data, cmd);
            })
        }
        if (cmd == 'agenda') {
            $.get('json/agenda.json'+"?timestamp=" + new Date().getTime(), (data) => {
                this.showInfo(data[0].agendaDetailEntities, cmd);
            })
        }
        if (cmd == 'courses') {
            $.get('json/agenda.json'+"?timestamp=" + new Date().getTime(), (data) => {
                let opens = [];
                for (var i = 0; i < data.length; i++) {
                    if (data[i].forum_type == 2) {
                        opens.push(data[i]);
                    }
                }
                this.showInfo(opens, cmd)
            })
        }
        if (cmd == 'where') {
            this.showInfo([{
                ch: '7',
                en: 'China National Convention Center (CNCC): No. 7 Tiancheng East Rd'
            }], cmd)
        }
        if (cmd == 'help') {
            this.outPutList.push({
                help: 'Ticket, invoice and related service, please email to create_help@baidu.com'
            })
        }
        if (cmd == 'whoami') {
            this.outPutList.push({
                developer: 'developer'
            })
        }
        if (cmd == 'ls') {
            this.outPutList.push({
                ls: ['create2017', 'create2018', 'create2019']
            })
        }
        if (cmd == 'pwd') {
            this.outPutList.push({
                pwd: '/home/developer/'
            })
        }
        if (cmd == 'll') {
            let data = ['drwxrwxr-x 5 developer baidu 4096 May  7 10:24 ./',
                'drwxr-xr-x 3 developer baidu 4096 May  7 10:24 ../',
                'drwxrwxr-x 2 developer baidu 4096 Jul  5 10:24 create2017/',
                'drwxrwxr-x 2 developer baidu 4096 Jul  4 10:24 create2018/',
                'drwxrwxr-x 2 developer baidu 4096 Jul  3 10:24 create2019/'
            ]
            this.showInfo(data,cmd)
        }
        this.setState({
            outPutList: this.outPutList,
        }, () => {
            // this.refs.TerminalInput.intoView();
            this.intoView();
        })
    }

    showInfo = (data, cmd) => {
        let n = 0;
        let animate = setInterval(() => {
            if (cmd == 'speakers') {
                if (n == 0) {
                    this.outPutList.push({
                        speakers: data[n], speakersFirst: `
  ____  ____  _____    _    _  _______ ____  ____  
 / ___||  _ \\| ____|  / \\  | |/ / ____|  _ \\/ ___| 
 \\___ \\| |_) |  _|   / _ \\ | ' /|  _| | |_) \\___ \\ 
  ___) |  __/| |___ / ___ \\| . \\| |___|  _ < ___) |
 |____/|_|   |_____/_/   \\_\\_|\\_\\_____|_| \\_\\____/ 
                    `
                    })
                } else {
                    this.outPutList.push({speakers: data[n]})
                }
            }
            if (cmd == 'agenda') {
                if (n == 0) {
                    this.outPutList.push({
                        agenda: data[n], agendaFirst: `
     _    ____ _____ _   _ ____    _    
    / \\  / ___| ____| \\ | |  _ \\  / \\   
   / _ \\| |  _|  _| |  \\| | | | |/ _ \\  
  / ___ \\ |_| | |___| |\\  | |_| / ___ \\ 
 /_/   \\_\\____|_____|_| \\_|____/_/   \\_\\
                    `
                    })
                } else {
                    this.outPutList.push({agenda: data[n]})
                }
            }
            if (cmd == 'courses') {
                if (n == 0) {
                    this.outPutList.push({
                        courses: data[n], coursesFirst: `
   ____ ___  _   _ ____  _____ ____  
  / ___/ _ \\| | | |  _ \\| ____/ ___| 
 | |  | | | | | | | |_) |  _| \\___ \\ 
 | |__| |_| | |_| |  _ <| |___ ___) |
  \\____\\___/ \\___/|_| \\_\\_____|____/ 
                        
                    `
                    })
                } else {
                    this.outPutList.push({courses: data[n]})
                }
            }
            if (cmd == 'where') {
                this.outPutList.push({
                    where: data[n], whereFirst: `
  _     ___   ____    _  _____ ___ ___  _   _ 
 | |   / _ \\ / ___|  / \\|_   _|_ _/ _ \\| \\ | |
 | |  | | | | |     / _ \\ | |  | | | | |  \\| |
 | |__| |_| | |___ / ___ \\| |  | | |_| | |\\  |
 |_____\\___/ \\____/_/   \\_\\_| |___\\___/|_| \\_|
                    `
                })
            }
            if(cmd == 'll'){
                this.outPutList.push({
                    ll:data[n]
                })
            }
            this.nameFlag = false;
            this.setState({
                outPutList: this.outPutList,
                nameFlag: this.nameFlag
            }, () => {
                // this.refs.TerminalInput.intoView();
                this.intoView();
                if (n == data.length - 1) {
                    this.nameFlag = true;
                    this.setState({
                        nameFlag: this.nameFlag
                    })
                    clearTimeout(animate);
                }
                n++;

            })
        }, 200)

    }

    lottieRender = (box, path) => {
        this.outPutList.push({className: box});
        this.setState({
            outPutList: this.outPutList
        }, () => {
            let element = this.refs[box];
            var create = lottie.loadAnimation({
                container: element, // the dom element that will contain the animation
                renderer: 'svg',
                loop: true,
                autoplay: true,
                path: path,// the path to the animation json
            });
            var delay = setTimeout(() => {
                this.nameFlag = true;
                this.setState({
                    nameFlag: true
                })
                clearTimeout(delay)
                // this.refs.TerminalInput.intoView();
                this.intoView();
            }, 200)
        })
    }

    renderSlowLine = (a) => {
        let n = 0;
        let f = setInterval(() => {
            if (n < a.length) {
                this.outPutList.push({desc: a[n]});
                n++;
                this.setState({
                    outPutList: this.outPutList,
                })
            } else {
                clearTimeout(f);
                this.enterHandle('createStart');
            }
        }, 200)
    }

    showMoreCmd = () => {
        this.showMoreFlag = !this.showMoreFlag;
        this.setState({
            showMoreFlag: this.showMoreFlag
        })
    }

    toolBoxClick = (cmd) => {
        this.showMoreFlag = false;
        this.setState({
            showMoreFlag: this.showMoreFlag
        })
        if (this.nameFlag) {
            this.enterHandle(cmd);
        }
    }

    renderDoms = (item, key) => {
        if (item.result) {
            return (
                <div key={key} className='terminal-output' style={{
                    height: IsPcOrNot.get() ? '30px' : 'auto',
                    display: "flex",
                    alignItems: "center"
                }}>
                    <div className="command">
                        <span>{item.result}</span>
                    </div>
                </div>
            )
        }
        if (item.desc || item.desc == '') {
            return (
                <p key={key} style={{
                    margin: '0',
                    lineHeight: IsPcOrNot.get() ? '30px' : '.4rem',
                    height: item.desc == '' ? IsPcOrNot.get() ? '15px' : '.2rem' : 'auto'
                }}>
                    {item.desc}
                </p>
            )
        }
        if (item.cmdLog) {
            return (
                <div key={key} style={{width: '100%'}}>
                    <span style={{color: '#73c9e3'}}>{item.cmdLog.name}</span>
                    <span>@</span>
                    <span style={{color: '#78d065'}}>{item.cmdLog.ip}</span>
                    <span style={{color: '#fced78'}}>{item.cmdLog.symbol}</span>#
                    <span>{item.cmdLog.value}</span>
                </div>
            )
        }
        if (item.className) {
            if (item.className == 'createStartBox') {
                return (
                    <div key={key} style={{
                        width: IsPcOrNot.get() ? '90%' : '100%',
                        height: IsPcOrNot.get() ? 'auto' : 'auto',
                        margin:'0 auto'
                    }} ref={item.className}></div>
                )
            }
            if (item.className == 'create-box') {
                return (
                    <div key={key} ref={item.className} style={{
                        width: 'auto',
                        height: '100%',
                        margin:'0 auto'
                    }} className='create-box'></div>
                )
            }
            if (item.className == 'infoBox') {
                return (
                    <div key={key} ref={item.className} style={{
                        width: IsPcOrNot.get()?'450px':'auto',
                        height:IsPcOrNot.get()?'auto':'100%',
                        margin:'0 auto'
                    }}>
                    </div>
                )
            }
        }

        if (item.progress == 'register') {
            return (
                <div key={key} ref={item.progress} style={{
                    width: '100%',
                    padding: IsPcOrNot.get() ? '15px 0' : '.1rem 0',
                    fontSize: IsPcOrNot.get() ? '20px' : '12px'
                }}>[{this.progressSymbol}]{this.progressNumber}%
                </div>
            )
        }

        if (item.speakers) {
            return (
                <div key={key} style={{
                    padding: '.3rem 0',
                    borderBottom: '1px dashed #fff'
                }}>
                    {
                        item.speakersFirst ? <div style={{
                            borderTop: '1px dashed #fff',
                            borderBottom: '1px dashed #fff'
                        }}>
                            <pre style={IsPcOrNot.get() ? {} : {
                                transform: 'scale(0.75)',
                                transformOrigin: 'left'
                            }}>
                            {item.speakersFirst}
                        </pre>
                        </div> : null
                    }
                    <div
                        style={{padding: '.15rem 0'}}>Name: {localStorage['language'] == 'en' ? item.speakers.name_en : item.speakers.name_ch}</div>
                    <div>
                        Title: {localStorage['language'] == 'en' ? item.speakers.desc_en : item.speakers.desc_ch}
                    </div>
                </div>
            )
        }
        if (item.agenda) {
            return (
                <div key={key} style={{
                    padding: '.3rem 0',
                    borderBottom: '1px dashed #fff'
                }}>
                    {
                        item.agendaFirst ? <div style={{
                            borderTop: '1px dashed #fff',
                            borderBottom: '1px dashed #fff'
                        }}>
                            <pre style={IsPcOrNot.get() ? {} : {
                                transform: 'scale(0.9)',
                                transformOrigin: 'left'
                            }}>
                            {item.agendaFirst}
                        </pre>
                        </div> : null
                    }
                    <div style={{padding: '.15rem 0'}}>
                        {item.agenda.agenda_time}
                    </div>
                    <div style={{padding: '.15rem 0'}}>
                        {localStorage['language'] == 'en' ? item.agenda.title_en : item.agenda.title_ch}
                    </div>
                    <div style={{padding: '.15rem 0'}}>
                        {localStorage['language'] == 'en' ? item.agenda.speaker_en : item.agenda.speaker_ch}
                    </div>
                    <div style={{padding: '.15rem 0'}}>
                        {localStorage['language'] == 'en' ? item.agenda.desc_en : item.agenda.desc_ch}
                    </div>
                </div>
            )
        }
        if (item.courses) {
            return (
                <div key={key} style={{
                    padding: '.3rem 0',
                    borderBottom: '1px dashed #fff'
                }}>
                    {
                        item.coursesFirst ? <div style={{
                            borderTop: '1px dashed #fff',
                            borderBottom: '1px dashed #fff'
                        }}>
                            <pre>
                            {item.coursesFirst}
                            </pre>
                        </div> : null
                    }
                    <div style={{padding: '.15rem 0'}}>
                        Title: {localStorage['language'] == 'en' ? item.courses.title_en : item.courses.title_ch}
                    </div>
                </div>
            )
        }
        if (item.where) {
            return (
                <div key={key} style={{
                    padding: '.3rem 0 0 0',
                    borderBottom: '1px dashed #fff'
                }}>
                    {
                        item.whereFirst ? <div style={{
                            borderTop: '1px dashed #fff',
                            borderBottom: '1px dashed #fff'
                        }}>
                            <pre style={IsPcOrNot.get() ? {} : {
                                transform: "scale(0.8)",
                                transformOrigin: "left"
                            }}>
                            {item.whereFirst}
                            </pre>
                        </div> : null
                    }
                    <div style={{padding: '.15rem 0'}}>
                        {localStorage['language'] == 'en' ? item.where.en : item.where.ch}
                    </div>
                </div>
            )
        }
        if (item.help) {
            return (
                <div key={key}>
                    {item.help}
                </div>
            )
        }
        if (item.developer) {
            return (
                <div key={key}>
                    {item.developer}
                </div>
            )
        }
        if (item.ls) {
            return (
                <div key={key}>
                    <span style={{padding: '0 .2rem 0 0'}}>{item.ls[0]}</span>
                    <span style={{padding: '0 .2rem'}}>{item.ls[1]}</span>
                    <span style={{padding: '0 .2rem'}}>{item.ls[2]}</span>
                </div>
            )
        }
        if (item.pwd) {
            return (
                <div key={key}>{item.pwd}</div>
            )
        }
        if (item.ll) {
            return (
                <div key={key}>
                    {item.ll}
                </div>
            )
        }
    }

    render() {
        return (
            <div className='terminal-c' ref='container' style={{
                width: '100%',
                height: '100%',
            }}>
                <div className="terminal" ref='terminal' style={{
                    position: 'absolute',
                    top: IsPcOrNot.get() ? '0' : '.2rem',
                    bottom: IsPcOrNot.get() ? '35px' : '1rem',
                    left: 0,
                    right: 0,
                    padding: '0 .31rem',
                    overflow: 'auto'
                }}>
                    {
                        this.outPutList.map((item, key) => {
                            return (
                                this.renderDoms(item, key)
                            )
                        })
                    }
                    <div style={{
                        position: 'relative',
                        marginTop: IsPcOrNot.get() ? '5px' : ''
                    }}>
                        <TerminalInput ref='TerminalInput' outPutList={this.outPutList}
                                       changeOutPut={this.changeOutPut}
                                       nameFlag={this.nameFlag}
                                       renderSlowLine={this.renderSlowLine}
                                       enterHandle={this.enterHandle}/>
                    </div>
                </div>
                <div style={{
                    position:IsPcOrNot.get()? 'absolute':'fixed',
                    bottom: '.31rem',
                    right: '.31rem',
                    borderTop: "1px solid #fff",
                    background: 'rgba(0,0,0,0.8)',
                    cursor: 'pointer',
                    zIndex:"9999999",
                }}>
                    <div style={{
                        display: this.showMoreFlag ? 'block' : 'none'
                    }}>
                        {
                            this.cmdList.map((item, key) => {
                                return (
                                    <div onClick={this.toolBoxClick.bind(this, item)} key={key} style={{
                                        width: IsPcOrNot.get() ? '100px' : '1.4rem',
                                        height: IsPcOrNot.get() ? '35px' : '.6rem',
                                        border: '1px solid #fff',
                                        boxSizing: 'border-box',
                                        borderTop: item == 0 ? '1px solid #fff' : '0',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}>
                                        {item}
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div onClick={this.showMoreCmd} style={{
                        width: IsPcOrNot.get() ? '100px' : '1.4rem',
                        height: IsPcOrNot.get() ? '35px' : '.6rem',
                        border: '1px solid #fff',
                        boxSizing: 'border-box',
                        zIndex: '999',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        flexWrap: "wrap",
                        fontSize: IsPcOrNot.get() ? '15px' : "14px",
                        borderTop: '0'
                    }}>
                        <span>toolbox</span>
                        <span style={IsPcOrNot.get() ? {
                            width: "0",
                            height: '0',
                            borderRight: "10px solid transparent",
                            borderLeft: '10px solid transparent',
                            borderTop: this.showMoreFlag ? '10px solid white' : '',
                            borderBottom: this.showMoreFlag ? '' : '10px solid white',
                            marginLeft: '.05rem'
                        } : {
                            width: "0",
                            height: '0',
                            borderRight: ".14rem solid transparent",
                            borderLeft: '.14rem solid transparent',
                            borderTop: this.showMoreFlag ? '.14rem solid white' : '',
                            borderBottom: this.showMoreFlag ? '' : '.14rem solid white',
                            marginLeft: '.05rem'
                        }}>
                    </span>
                    </div>
                </div>
            </div>
        )
    }
}

class PC extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div style={{backgroundColor: '#000000', minHeight: "100%"}}>
                <Header/>
                <div className='pc'>
                    <TerminalOutput/>
                </div>
            </div>
        )

    }
}

class Phone extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div ref='phone' style={{backgroundColor: '#000000', minHeight: '100%'}}>
                <Header/>
                <div className='phone'>
                    <TerminalOutput/>
                </div>
            </div>
        )
    }
}

class Terminal extends Component {
    render() {
        return (
            IsPcOrNot.get() ? <PC/> : <Phone/>
        )
    }
}

export {TerminalOutput}
export default Terminal




